
<template>
  <div class="wrap-category">
    <!--面包导航-->
    <div class="nav-bar clear-fix">
      <el-breadcrumb separator=">" class="fl-l">
        <el-breadcrumb-item>商品管理</el-breadcrumb-item>
        <el-breadcrumb-item>商品添加</el-breadcrumb-item>
        <el-breadcrumb-item>选择分类</el-breadcrumb-item>
      </el-breadcrumb>   
      
    </div>
    <div class="content">
        <div class="most-category">
            <span class="item-label">常用类目：</span>
            <el-select v-model="mostCat" size="small" clearable filterable @change="showSelect">
                <el-option :value="item.cat_id" v-for="item in mostSelectList" :key="item.cat_id" :label="item.cat_names"></el-option>
            </el-select>
        </div>
        <div class="select-category">
            <span class="item-label">选择类目：</span>
            <el-cascader
            :options="cateList"
            v-model="selectId"
            :props="props"
            size="small"
            clearable
            filterable
            :separator="'>>'"
            ref="cascader"
            @change="showSelect"

            ></el-cascader>
        </div>
        <div class="selected-box">
            <span>您当前的选择：</span>{{showStr}}
        </div>
        <div class="submit">
            <el-button type="primary" size="small" @click="submit" :disabled="mostCat==''&&selectId.length==0">确定分类，下一步</el-button>
        </div>
        
    </div>
    
  </div>
</template>



<script>
export default {
  data(){
      return{
          showStr:'',
          mostCat:'',
          cateList:[],
          selectId:[],
          mostSelectList:[],
          props: {
            value: "catId",
            label: "catName"
         },
        
      }
  },
  computed:{

  },
  methods:{
    //获取商品分类列表
    getCateList() {
        this.axios.post( "/proxy/report/api/common/category/options",{ body: {} }).then(response => {
            let data = response.data;
            if (data.head.code == "10000") {
                this.cateList = data.body;
            }
        });
    },
    getMostSelectList(){
        this.axios.post( "/proxy/fv/supplier/goods.php?act=get_supplier_category",{ body: {} }).then(response => {
            let data = response.data;
            if (data.head.code == "10000") {
                this.mostSelectList=data.body.data;    
            }
        });        
    },
    submit(){
        let id='';
        if(this.mostCat!=''){
            id=this.mostCat
        }else{
            id=this.selectId[this.selectId.length-1]
        }
       this.$router.push('/vendor/goods-add/add?cate='+id);
    },
    showSelect(){
        this.showStr='';
        if(this.mostCat!=''){
            for(let i in this.mostSelectList){
                if(this.mostCat==this.mostSelectList[i].cat_id){
                    this.showStr=this.mostSelectList[i].cat_names;
                    break;
                }
            }
          
        }else{
            console.log(this.$refs.cascader)
            if(this.$refs.cascader){
                this.showStr=this.$refs.cascader.currentLabels.join('>>');
            } 
            
        }
    }
  },  
  created(){
      this.getCateList();
      this.getMostSelectList();
  }
}
</script>

<style lang="less" >
    .wrap-category{
        color:#222;
        font-size: 16px;
        padding-right: 15px;        
        .most-category{
            margin-top:30px;
            margin-bottom: 30px;
            .el-select{
                width: 70%;
            }
        }
        .select-category{
            padding-bottom: 60px;
            .el-cascader{
                width: 70%;
            }
            
        }
        .selected-box{
            font-size: 12px;
            margin-top: 12px;
            background: #FFF4E2;
            color: #EC5F28;
            padding-left: 24px;
            height: 52px;
            line-height: 52px;
            border: 1px solid #FFE4B5;
            font-weight: bold; 
            width: 74%;           
        }
        .submit{
            padding:60px 0;
            text-align: center;
        }
        .el-select-dropdown__item.selected,.el-cascader-menu__item.is-active{
        color: #EC5F28;
    }
        .el-cascader-menu{
        height: 400px;
    }
    .el-cascader-menu__item{
        padding-left: 15px;
        padding-right: 30px;
    }
        
    }
    

</style>



