<template>
  <div class="goods-add">
    <!--面包导航-->
    <div class="nav-bar clear-fix">
      <el-breadcrumb separator=">" class="fl-l">
        <el-breadcrumb-item>商品管理</el-breadcrumb-item>
        <el-breadcrumb-item>商品编辑</el-breadcrumb-item>
      </el-breadcrumb>   
      
    </div>
    <div class="content">
        <el-form ref="form-add" :model="addForm" label-width="120px" size="mini" class="form-add">
            <!-- 商品分类 -->
            <div class="chunk chunk-category">
                <div class="chunk-title">编辑商品</div>
                <div class="chunk-content">              
                    <div class="category">
                         您当前选择的类目是 
                         <span class="color-info">
                             <span v-for="it in goodsInfo.category" :key="it.cat_id">{{it.is_last==1?it.cat_name:(it.cat_name+'>>')}}</span>
                         </span>
                         <router-link to="/vendor/goods-category" style="margin:0 15px;color:#222;" v-if="actionType!='edit'">重选类目</router-link>
                         <a href="http://res.intmall.net/file/vendors/product_upload.pdf" class="color-info" target="_blank">操作指引</a>
                    </div>           
                </div>
            </div>
            <!-- 商品名称描述 -->
            <div class="chunk chunk-desc" id="chunk-desc" tabindex="22">
                <div class="chunk-title">商品名称描述</div>
                <el-form-item label="商品名称" :rules="[{ required: true, message: '请输入商品名称'},{pattern: /^[^\u4e00-\u9fa5]+$/, message: '该字段不能输入中文'}]"  prop="common.name">
                    <el-input v-model="addForm.common.name" maxlength="128" tabindex="22" id="tab-common.name"></el-input>
                    <div class="form-item-tip">品牌+型号/材质/功能+商品名称，不可出现重复词，注意单词罗列顺序，<span class="color-danger">128个字符以内</span>.请仔细阅读<a href="http://res.intmall.net/file/vendors/title_rules.pdf" class="color-info" target="_blank">标题命名规则</a>.</div>
                </el-form-item>
                <el-form-item label="申报品名(英文)" :rules="[{ required: true, message: '请输入申报品名(英文)'},{pattern: /^(?![0-9\s,\(\)%]+$)[0-9\s,\(\)%a-zA-Z]{1,}$/, message: '申报品名（英文）只能为英文、英文和数字、空格以及符号 (),%的组合'}]"  prop="common.short_name">
                    <el-input v-model="addForm.common.short_name" maxlength="35" tabindex="22" id="tab-common.short_name"></el-input>
                    <div class="form-item-tip">该字段仅用于打印面单及清关使用，<span class="color-danger">35个字符以内.</span></div>
                </el-form-item>
                <el-form-item label="申报品名(中文)" :rules="[{ required: true, message: '请输入申报品名(中文)'},{pattern: /^(?![0-9a-zA-Z\s]+$)[\u4e00-\u9fa50-9a-zA-Z\s]{1,}$/, message: '申报品名（中文）只能为中文、中文+数字的组合、中文+英文的组合'}]"  prop="common.declare_name">
                    <el-input v-model="addForm.common.declare_name" maxlength="20" tabindex="22" id="tab-common.declare_name"></el-input>
                      <div class="form-item-tip">该字段用于大陆海关清关使用，<span class="color-danger">20个字符以内.</span></div>              
                </el-form-item>
                <el-form-item label="商品详细描述" required="">
                    <div class="editor-box" tabindex="22" id="tab-common.desc">
                       <vue-html5-editor :content="addForm.common.desc" :height="300" :auto-height="false" @change="editorChange" ref="editor-h5"></vue-html5-editor>
                       <div class="form-item-tip"><span class="color-danger">详情描述禁止商家填写联系方式！</span> 请注意：英文单词拼写，语法；商品重要参数信息请以<span class="color-danger">表格或文本形式</span>呈现；服饰类尺码信息以<span class="color-danger">表格形式</span>呈现，单位为<span class="color-danger">cm</span>，俄罗斯 不用<span class="color-danger">inch</span>.</div>
                    </div>
                </el-form-item>               
                <el-form-item label="商品简要描述" :rules="[{ required: true, message: '请输入商品简要描述'},{pattern: /^[^\u4e00-\u9fa5]+$/, message: '该字段不能输入中文'}]"  prop="common.short_desc" >
                    <el-input type="textarea" v-model="addForm.common.short_desc"  :rows="4" max="200" tabindex="22" id="tab-common.short_desc"></el-input>
                    <div class="form-item-tip">注意：短描述用在合作平台的推荐，不在前台页面显示，<span class="color-danger">200个字符以内.</span></div>
                </el-form-item>                
                <el-form-item label="包装描述" :rules="[{ required: true, message: '请输入包装描述'},{pattern: /^[^\u4e00-\u9fa5]+$/, message: '该字段不能输入中文'}]"  prop="common.package_desc">
                    <el-input v-model="addForm.common.package_desc" tabindex="22" id="tab-common.package_desc" maxlength="255" ></el-input>
                    <div class="form-item-tip">格式如：<span class="color-info">Cellphone x1, Charger x1, USB Cable x1, Quick Guide x1, Service Manual x1.</span></div>
                </el-form-item>
            </div>
           <!-- 商品基本信息 -->
            <div class="chunk chunk-basic" id="chunk-basic" name="chunk-basic" tabindex="22">
                <div class="chunk-title">商品基本信息</div>
                <el-form-item label="商品货号"  :rules="[{pattern: /^[^\u4e00-\u9fa5]+$/, message: '该字段不能输入中文'}]" prop="common.product_code">
                    <el-input  v-model="addForm.common.product_code"  tabindex="22" id="tab-common.product_code"></el-input>
                </el-form-item>
                <el-form-item label="品牌" :rules="[{ required: true, message: '请选择品牌'}]"  prop="common.brand_id">
                    <el-select v-model="addForm.common.brand_id" size="small" clearable :disabled="actionType=='edit'"  tabindex="22" id="tab-common.brand_id">       
                        <el-option :value="item.brandId" :label="item.brandName" v-for="item in brandsList" :key="item.brandId"></el-option>
                    </el-select>                    
                </el-form-item> 
                <el-form-item label="单位" :rules="[{ required: true, message: '请选择单位'}]"  prop="common.goods_units_val">
                    <el-select v-model="addForm.common.goods_units_val" size="small" clearable tabindex="22" id="tab-common.goods_units_val" >       
                        <el-option :value="item.en_name" :label="item.cn_name" v-for="item in unitList" :key="item.cn_name"></el-option>
                    </el-select>                    
                </el-form-item>
                <el-form-item label="市场价" :rules="[{ required: true, message: '请输入售价'},{pattern: /(^([1-9]\d*(\.\d{1,2})?)$)|(^(0\.[1-9][0-9]{0,1})$)|(^(0\.0[1-9])$)/, message: '请输入大于零的两位小数'}]"  prop="common.market_price">
                    <el-input v-model="addForm.common.market_price"  size="small" tabindex="22" id="tab-common.market_price"></el-input>
                </el-form-item> 
                <el-form-item label="售价" :rules="[{ required: true, message: '请输入售价'},{pattern:/(^([1-9]\d*(\.\d{1,2})?)$)|(^(0\.[1-9][0-9]{0,1})$)|(^(0\.0[1-9])$)/, message: '请输入大于零的两位小数', trigger: ['blur', 'change']}]"  prop="common.shop_price">
                    <el-input v-model="addForm.common.shop_price"  :controls="false" tabindex="22" id="tab-common.shop_price"></el-input>
                    <div class="form-item-tip">注意：当多个SKU售价不同时，售价必须>=最低售价！</div>
                </el-form-item>
                <el-form-item label="商品有效期" :rules="[{ required: true, message: '请选择商品有效期'}]"  prop="common.expire_days">
                    <i class="iconfont icon-help color-help" @click="showHelp('expire_day')"></i>
                    <el-radio v-model="addForm.common.expire_days" :label="1">30天</el-radio>
                    <el-radio v-model="addForm.common.expire_days" :label="2">60天</el-radio>
                </el-form-item>
                <el-form-item label="是否带电" :rules="[{ required: true, message: '请选择是否带电'}]"  prop="common.is_elec">
                    <el-switch style="display: block" v-model="addForm.common.is_elec" active-color="#13ce66" inactive-color="#ccc"  :active-value="'1'" :inactive-value="'0'"></el-switch>
                </el-form-item>
                <el-form-item label="MSDS的有效时间" >    
                    <el-date-picker style="width: 100%;" v-model="addForm.common.msds" type="datetime" placeholder="选择日期时间" tabindex="22" id="tab-common.msds" value-format="yyyy-MM-dd HH:mm:ss"></el-date-picker>
                </el-form-item>                                        
            </div>
           <!-- 包装信息 -->
            <div class="chunk chunk-package" id="chunk-package" tabindex="22">
                <div class="chunk-title">包装信息</div>
                <el-form-item label="商品包装后的重量" :rules="[{ required: true, message: '请输入商品包装后的重量'},{pattern: /(^([1-9]\d*(\.\d{1,3})?)$)|(^0\.[1-9][0-9]{0,2}$)|(^0\.0[1-9][0-9]{0,1}$)|(^0.00[1-9]$)/, message: '输入大于零的三位小数', trigger: ['blur', 'change']}]"  prop="common.goods_weight" class="item-goods-weight" >
                    <i class="iconfont icon-help color-help" @click="showHelp('package')"></i>    
                    <el-input v-model="addForm.common.goods_weight" size="small"  class="item-weight" tabindex="22" id="tab-common.goods_weight"></el-input>
                    <span class="show-unit">公斤(KG) / 件</span>
                </el-form-item>   
                <el-form-item label="商品包装后的尺寸" required class="item-size">
                    <el-col :span="0.5"><i class="iconfont icon-help color-help" @click="showHelp('package')"></i></el-col>
                    <el-col :span="4">
                        <el-form-item prop="common.package_length" :rules="[{ required: true, message: '该字段必须填写'},{pattern: /(^0$)|(^[1-9][0-9]*$)/, message: '请输入非负整数', trigger: ['blur', 'change']}]">
                           
                            <el-input v-model="addForm.common.package_length"  size="small" tabindex="22" id="tab-common.package_length"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col class="line" :span="1">X</el-col>
                    <el-col :span="4">
                        <el-form-item prop="common.package_width" :rules="[{ required: true, message: '该字段必须填写'},{pattern: /(^0$)|(^[1-9][0-9]*$)/, message: '请输入非负整数', trigger: ['blur', 'change']}]">
                            <el-input v-model="addForm.common.package_width"  size="small" tabindex="22" id="tab-common.package_width"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col class="line" :span="1">X</el-col>
                    <el-col :span="4">
                        <el-form-item prop="common.package_height" :rules="[{ required: true, message: '该字段必须填写'},{pattern: /(^0$)|(^[1-9][0-9]*$)/, message: '请输入非负整数', trigger: ['blur', 'change']}]">
                            <el-input v-model="addForm.common.package_height"  size="small" tabindex="22" id="tab-common.package_height"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col class="show-unit" :span="4">(单位:厘米)</el-col>                                        
                </el-form-item>                              
            </div>            
           <!-- 设置运费 -->
            <div class="chunk chunk-package" id="chunk-freight" tabindex="22">
                <div class="chunk-title">设置运费</div>
                <el-form-item label="运费模板" :rules="[{ required: true, message: '请选择运费模板'}]"  prop="common.freight_template_id" class="item-freight">
                    <el-select v-model="addForm.common.freight_template_id" size="small" clearable class="item-mid-width" @visible-change="showFreightTemplate" tabindex="22" id="tab-common.freight_template_id">       
                        <el-option :value="item.id" :label="item.templateTitle" v-for="item in freightTemplateList" :key="item.id"></el-option>
                    </el-select>
                    <a href="/vendor/freight/add" class="color-info" target="_blank">添加运费模板</a>  
                    <a href="/vendor/freight-list" class="color-info" target="_blank">管理运费模板</a>  
                    <a href="http://res.intmall.net/file/vendors/freight-guide.pdf" class="color-info" target="_blank">运费模板设置操作指引</a>                                  
                </el-form-item>                 
            </div>
           <!-- 商品属性 -->
            <div class="chunk chunk-attr" id="chunk-attrs" tabindex="22">
                <div class="chunk-title">商品属性</div> 
               
                <!-- 
                    attr_input_type==0  input  
                    attr_input_type==1  && attr_type==2 checkbox 
                    attr_input_type==1  && attr_type==0 select
                    attr_input_type==2  textarea                       
                -->
                <div class="attr-item-box" >
                    <div class="attr-item" v-for="item in attrList" :key="item.attr_id">
                        <div v-if="item.attr_input_type=='0'">
                            <el-form-item :label="item.attr_name"  :rules="[{pattern: /^[^\u4e00-\u9fa5]+$/, message: '该字段不能输入中文'}]" :prop="'attrs.'+item.attr_id">
                                <el-input  v-model="addForm.attrs[item.attr_id]" tabindex="22" :id="'tab-attrs.'+item.attr_id"></el-input>
                            </el-form-item>                            
                        </div>
                        <div v-if="item.attr_input_type=='2'">
                            <el-form-item :label="item.attr_name"  :rules="[{pattern: /^[^\u4e00-\u9fa5]+$/, message: '该字段不能输入中文'}]" :prop="'attrs.'+item.attr_id">
                                <el-input type="textarea"  v-model="addForm.attrs[item.attr_id]" :rows="4"  tabindex="22" :id="'tab-attrs.'+item.attr_id"></el-input>
                            </el-form-item>                            
                        </div>
                        <div v-if="item.attr_input_type=='1' && item.attr_type==0">
                            <el-form-item :label="item.attr_name"  >
                                <el-select v-model="addForm.attrs[item.attr_id]" size="small" clearable  >       
                                    <el-option :value="index2" :label="item2.val" v-for="(item2,index2) in item.attr_values" :key="index2"></el-option>
                                </el-select>
                            </el-form-item>                                                        
                        </div>
                        <div v-if="item.attr_input_type=='1' && item.attr_type==2">
                            <el-form-item :label="item.attr_name"   >
                                <el-checkbox-group v-model="addForm.attrs[item.attr_id]">
                                    <el-checkbox :label="index2" v-for="(item2,index2) in item.attr_values" :key="index2">{{item2.val}}</el-checkbox>
                                </el-checkbox-group>
                            </el-form-item>                                                        
                        </div>                        
                    </div>
                </div>
            </div>
           <!-- SKU管理 -->
            <div class="chunk chunk-price-attr" id="chunk-sku" tabindex="22">
                <div class="chunk-title">SKU管理</div> 
                <div class="chunk-content">
                    <div class="price-attr-first" style="width:80%;">
                        <div class="item-title">
                            自定义颜色属性 <a href="/goods/upload-help#chunk-color" class="color-info" target="_blank" style="margin-left:15px;">颜色参考</a> 
                            
                            <el-button type="primary" size="mini" class="item-button"  @click="attr_price_add('price_attr_first')">增加属性</el-button>
                        </div>
                        <div style="font-size:12px;margin-bottom:15px;"  class="color-danger">当平台推荐的属性值无法满足您对产品描述时，可以添加自定义属性！</div>
                        <div class="item-table" tabindex="22" id="tab-attr-pic">
                            <el-table :data="addForm.color_images" border  style="width: 100%" :row-key="'gallery_id'">    
                                <el-table-column prop="color"  label="自定义颜色属性"  align="center" width="250">   
                                    <template slot-scope="scope">                                                                           
                                        <el-autocomplete
                                        size="small"
                                        class="inline-input"
                                        v-model.trim="scope.row.color"
                                        @blur="atttr_price_unique(scope.$index,scope.row.color)"
                                        @select="atttr_price_unique(scope.$index,scope.row.color)"              
                                        :fetch-suggestions="querySearch"
                                        :disabled="(scope.row.gallery_id+'').indexOf('new_')==-1&&actionType=='edit'"
                                        :maxlength="30"
                                        ></el-autocomplete>
                                                                             
                                    </template>
                                </el-table-column>
                                <el-table-column  label="自定义图片(jpg,<2M)" min-width="300" align="center">  
                                    <template slot-scope="scope">                                        
                                        <el-upload
                                            :ref="'attr-upload-'+scope.$index"
                                            class="avatar-uploader"
                                            :action="'/proxy/fv/supplier/goods.php?act=uploadImage&goods_id='+addForm.goods_id"
                                            :show-file-list="true"
                                            :on-success="handleAvatarSuccess" 
                                            :list-type="'picture-card'"                                       
                                            :on-remove="handleAvatarRemove"
                                            :on-exceed='handleAvatarExceed'
                                            :limit="1"
                                            :file-list='color_images_fileList[scope.$index]'  
                                            :before-upload="beforeAvatarUpload" >                                           
                                            <el-button size="mini" type="primary">点击上传</el-button>    
                                        </el-upload>
                                    </template>
                                    
                                </el-table-column>
                                <el-table-column   label="操作"  align="center" width="100">  
                                    <template slot-scope="scope" v-if="!((scope.row.gallery_id+'').indexOf('new_')==-1&&actionType=='edit')">
                                       <el-button type="danger" size="mini" @click="attr_price_delete(scope.$index)">删除</el-button>
                                    </template>
                                </el-table-column>                                
                            </el-table>  
                                                
                        </div>

                    </div>

                    <div class="sku-list-box" >
                        <div class="sku-title">sku 列表管理 <a href="/goods/upload-help#chunk-attr" class="color-info" target="_blank" style="margin-left:15px;">属性值信息</a> </div>
                        <div></div>
                        <div class="sku-batch" tabindex="22" id="tab-skus">
                            <span class="batch-label">批量修改增量价格</span>
                            <el-input v-model="skuBatch.product_price" size="mini" class="min-width" @blur="checkNumber('skuBatch.product_price',2)" ></el-input>
                            <el-button plain type="info" size="mini" @click="skuBatchAction('product_price')">确定</el-button>
                            <span class="batch-label" style="margin-left:60px;">批量修改库存</span>
                            <el-input v-model="skuBatch.product_number" size="mini" class="min-width" @blur="checkNumber('skuBatch.product_number',0)"></el-input>
                            <el-button plain type="info" size="mini" @click="skuBatchAction('product_number')">确定</el-button>
                            <el-button size="mini" class="item-button" type="primary" @click="actionSku('add')">增加sku</el-button>
                        </div>
                        <el-table :data="addForm.skus" border  style="width: 100%" :row-key="'product_id'">    
                                <el-table-column prop="name"  label="sku 货号"  align="center">  
                                    <template slot-scope="scope">
                                        <el-input v-model="scope.row.product_title" size="small" placeholder="sku 货号" :disabled="(scope.row.product_id+'').indexOf('new_')==-1" ></el-input>
                                    </template>                                    
                                </el-table-column>
                                <el-table-column prop="name"  label="颜色属性"  align="center">  
                                    <template slot-scope="scope">
                                        <el-select v-model="scope.row.color" size="small" :disabled="(scope.row.product_id+'').indexOf('new_')==-1&&actionType=='edit'">
                                            <el-option  v-for="item in addForm.color_images"  :key="item.gallery_id"  :label="item.color"  :value="item.color" v-if="item.color.trim()!=''"  ></el-option>
                                        </el-select>                                       
                                    </template>                                    
                                </el-table-column>
                                <el-table-column prop="name"  :label="price_attr_second"  align="center" v-if="price_attr_second!=''">  
                                    <template slot-scope="scope">
                                        <el-input v-model="scope.row.size" size="small" :placeholder="price_attr_second" :disabled="(scope.row.product_id+'').indexOf('new_')==-1&&actionType=='edit'" @blur="checkSkuAttr(scope.$index)" maxlength="30"></el-input>
                                    </template>                                    
                                </el-table-column>
                                <el-table-column prop="name"  label="增量价格"  align="center">  
                                    <template slot-scope="scope">
                                        <el-input v-model="scope.row.product_price" size="small" class="min-width" @blur="checkSkuNumber('product_price',2,scope.$index)"></el-input>
                                    </template>                                    
                                </el-table-column>
                                
                                <el-table-column prop="name"  label="基础售价"  align="center" width="80">  
                                    <template slot-scope="scope">
                                       {{addForm.common.shop_price}}
                                    </template>                                    
                                </el-table-column>
                                <el-table-column prop="name"  label="sku 价格"  align="center" width="80">  
                                    <template slot-scope="scope">                                       
                                         {{isNaN((Number(addForm.common.shop_price)+Number(scope.row.product_price)))?'':(Number(addForm.common.shop_price)+Number(scope.row.product_price)).toFixed(2)}}
                                    </template>                                    
                                </el-table-column>
                                <el-table-column prop="name"  label="库存"  align="center">  
                                    <template slot-scope="scope">
                                        <el-input v-model="scope.row.product_number" size="small"  class="min-width" @blur="checkSkuNumber('product_number',0,scope.$index)"></el-input>
                                    </template>                                    
                                </el-table-column>
                                <el-table-column prop="name"  label="操作"  align="center" width="100">  
                                    <template slot-scope="scope" v-if="!((scope.row.product_id+'').indexOf('new_')==-1&&actionType=='edit')" >
                                        <el-button type="danger" size="mini" @click="actionSku('delete',scope.$index)">删除</el-button>
                                    </template>                                    
                                </el-table-column>
                        </el-table>
                    </div>
                </div>
                 
            </div>
           <!-- 商品图片 -->
            <div class="chunk chunk-product-pic" id="chunk-goods-pic" tabindex="22">
                <div class="chunk-title">商品图片</div> 
                <div class="chunk-tip">
                    <p>注意：</p>
                    <p>1、主图请排到第一张,点击图片可设置为主图！</p>
                    <p>2、单张图片大小不能超过2M，可以分批多次上传。</p>
                    <p>3、图片格式请使用JPG，图片大小设定至少500*500，白色背景，无水印且不带任何文字(可带有品牌logo但不可是中文)。</p>
                    <p>4、上传图片数量至少3张。如果不选择主图，则默认上传的第一张为主图。</p>            
                </div>
                <div class="chunk-content">
                    <div class="upload-box" tabindex="22" id="tab-product-pic">
                        <!-- <el-dialog :visible.sync="dialogVisible">
                            <img width="100%" :src="dialogImageUrl" alt="">
                        </el-dialog> -->
                        <div class="demo-upload-list" v-for="(item,index) in uploadList" :key="item.name">
                            <template v-if="item.status === 'finished'">
                                <img :src="item.url" @click="handleImg('master',item)">
                                <div class="master" v-if="index==0">主图</div>
                                <div class="demo-upload-list-cover">
                                
                                        <i class="el-icon-caret-left" @click="handleImg('left',item)" title="左移"></i>
                                        <i class="el-icon-caret-right" title="右移" @click="handleImg('right',item)"></i>
                                        <i class="el-icon-delete" @click="handleImg('remove',item)" title="删除"></i>
                                        <!-- <span @click="handleImg('master',item)">主图</span> -->
                                </div>
                            </template>
                            <template v-else>
                                <Progress v-if="item.showProgress" :percent="item.percentage" hide-info></Progress>
                            </template>
                        </div>
                        <Upload
                            ref="upload2"
                            :show-upload-list="false"
                            :default-file-list="defaultList"
                            :on-success="handleSuccess2"
                            :format="['jpg','jpeg','png']"
                            :max-size="2048"
                            :on-format-error="handleFormatError"
                            :on-exceeded-size="handleMaxSize"
                           
                            multiple
                            type="drag"
                            :action="'/proxy/fv/supplier/goods.php?act=uploadImage&goods_id='+addForm.goods_id"
                            style="display: inline-block;width:162px;">
                            <div style="width: 162px;height:192px;line-height: 192px;">                              
                                <i class="el-icon-plus" style="font-size:24px;"></i>
                            </div>
                        </Upload>                        
                    </div>

                </div>
                 
            </div>                                  
            <div class="submit-box">
                <div class="submit-button">
                    <el-button type="primary" @click="submit('draft')" size="small" v-if="actionType!='edit'" :disabled="submitLoading" >保存草稿</el-button>
                    <el-button type="danger" @click="submit('add')" size="small" :disabled="(goods_status==1||goods_status==2)||submitLoading" :loading="submitLoading">提交商品</el-button>
                    <el-button type="danger" @click="submit('audit')" size="small" :disabled="(goods_status==1||goods_status==2)||submitLoading" :loading="submitLoading">提交审核</el-button>
                </div>
                <div class="submit-tip" style="margin-left:44.2%;margin-top:25px;color:#222;">
                    <div class="item">提交商品：保存商品信息,不提交审核</div>
                    <div class="item">提交审核：保存商品信息,并提交审核</div>
                </div>
            
            </div>
        </el-form> 
        <div class="nav-open" v-if="!navInfo.navShow" @click="navInfo.navShow=true" title="展开导航">
            <i class="iconfont icon-fold-close"></i>
        </div>
        <transition name="el-zoom-in-center">  
            <div class="goods-add-nav" v-show="navInfo.navShow">
                <div class="nav-header">
                    <span>信息导航</span>
                    <i class="el-icon-circle-close el-icon" v-if="navInfo.navShow" @click="navInfo.navShow=false"></i>
                    <i class="el-icon-more el-icon"  v-if="!navInfo.navShow" @click="navInfo.navShow=true"></i>
                </div>
            
                    <div class="nav-body" >

                        <dl class="dl-info">
                            <dt @click="showSubnav(1)"> <i class="iconfont icon-nav " ></i>产品信息导航</dt>                       
                            <dd v-show="navInfo.showIndex==1" v-for="item in navInfo.list" :key="item.key" >
                                <a href="javascript:;"  :class="item.key==navInfo.hash?'color-info':''"  @click.stop="navJump(item.key)"><i class="iconfont icon-dot"></i>{{item.title}}</a>
                                <!-- :href="'#'+item.key" -->
                            </dd>                                         
                        </dl>
                        <dl class="dl-help">
                            <dt @click="showSubnav(2)"><i class="iconfont icon-nav"></i>帮助信息导航</dt>
                            <!-- <transition name="el-zoom-in-center"> -->
                                <dd v-show="navInfo.showIndex==2" v-html="navInfo.info">
                                    
                                </dd>
                            <!-- </transition> -->
                        </dl>

                    </div>
            
            </div>
        </transition>
        <!-- <div class="go-top">
            <i class="el-icon-caret-top"></i>
        </div> -->
        
    </div>

  </div>
</template>
<script>
import ColorList from '@/json/goods-color.json';
export default {
  data(){
      return {
          uploadList:[],
          defaultList:[ ],  

          submitLoading:false,
          userType:'',
          actionType:'',
          goods_status:7,
          addForm:{
            goods_id: '',
            draft_id:'',
            approval:false,
            cat_id:'',
            "common": {
                "name": "",
                "short_name": "",
                declare_name:'',
                "desc": "",
                "short_desc": "",
                "package_desc": "",
                "product_code": "",
                "brand_id": "",
                "goods_units_val": "",
                "market_price": '',
                "shop_price": '',
                "is_elec": 0,
                "msds": "",
                "goods_weight": '',
                "package_length": '',
                "package_width": '',
                "package_height": '',
                "freight_template_id": '',
                "expire_days": 2
            },
            "attrs": {
                
            },
            "skus": [],
            "color_images": [],
            common_images:[],
                       
        },
        brandsList:[],
        unitList:[],
        freightTemplateList: [],
        goodsInfo:{},
        attrList:[],//商品属性
        // 价格属性
        price_attr_second:'',//第二价格属性名称     
        price_upload_first:[],
        imageUrl: '',
        colorDeleteList:[],
        //sku 
        skuBatch:{
            product_price:'',
            product_number:'',
        },

        // 商品图片
        "common_fileList": [], 
         color_images_fileList:[],      
        product_files:[], 
        desc:'',
        //草稿详情
        draftDetail:{},
        navInfo:{
            navShow:true,
            showIndex:1,
            hash:'',
            infoType:{
                package:`<div  style="font-weight:700;color:#108ee9;">重量和包装尺寸:</div>
                        <div class="content-info">准确填写包装后重量和产品包装尺寸，避免因填写错误而造成的运费<span style="color:#108ee9;">损失和交易性降低</span>。</div>
                        <div style="font-weight:700;color:#108ee9;">运费计算方法:</div>
                        <div class="content-info">不同的物流公司计算运费的方式会有所不同。顺丰小包、E邮宝、中国邮政小包和香港小包等是以产品包装实重来计算运费。顺丰特惠、中国邮政EMS、新加坡EMS、DHL、利威国际物流专线、SEKO等会根据产品包装实重和产品包装体积重（材积）两者的较高值来计算运费。对于体积重大于实重的产品，请谨慎选择填写，可以计算出体积重后填写。体积重（材积）计算公式根据不同物流公司计算方式不同. <span style="color:#108ee9;">“长(cm)×宽(cm)×高(cm)÷5000=重量(KG)”或“ 长(cm)×宽(cm)×高(cm)÷6000=重量(KG)”</span></div>`,
                 expire_day:`<div>商品有效期是指，商品在网站展示的有效期，商品过期将自动下架！</div>`       
            },
            info:'',
            list:[{
                title:'商品名称描述',
                key:'chunk-desc'
            },
            {
                title:'商品基本信息',
                key:'chunk-basic'
            },
            {
                title:'包装信息',
                key:'chunk-package'
            },
            {
                title:'设置运费',
                key:'chunk-freight'
            },
            {
                title:'商品属性',
                key:'chunk-attrs'
            },
            {
                title:'SKU管理',
                key:'chunk-sku'
            },
            {
                title:'商品图片',
                key:'chunk-goods-pic'
            }
            
            ]
        }
    
      }
     
  },
  methods:{



    showSubnav(index){
        this.navInfo.showIndex=index;
    },
    showHelp(type){
        this.navInfo.showIndex=2;
        this.navInfo.info=this.navInfo.infoType[type];
    },
    editorChange(data){
       this.addForm.common.desc=data;
    },  
    //获取品牌
    getBrandList() {  
        this.axios.post("/proxy/fv/supplier/goods.php?act=getSupplierBrand",{}).then(res => {
            let data = res.data;
            this.brandsList = data.body.rows;
        });
      
    },
    //获取单位
    getUnitList() {  
        this.axios.post("/proxy/fv/supplier/goods.php?act=get_goods_units",{}).then(res => {
            let data = res.data;
            if (data.head.code == "10000") {
                this.unitList = data.body.gu_list;
            }
            
        });
      
    }, 
    // 获取运费模板方式
    getFreightTemplate() {
       this.axios.post("/proxy/freight/api/template/list", { body: { pager:{page: 1, pageSize: 1000 }} 
        }).then(response => {
            let data = response.data;
            if (data.head.code == "10000") {
                //去除默认运费模板
                let arr=JSON.parse(JSON.stringify(data.body.rows));
                let tmp=[];
                for(let i in arr){
                    if(arr[i].supplierId!='0'){
                        tmp.push(arr[i]);     
                    }
                }
                this.freightTemplateList=tmp;                 
            }
        });
    }, 
    //拉取运费模板
    showFreightTemplate(value){
        if(value){
            this.getFreightTemplate();
        }
    },
    //获取商品属性
    getGoodInfo(){
        this.axios.post('/proxy/fv/supplier/goods.php?act=get_attribute',{body:{id:this.addForm.cat_id,gid:this.addForm.goods_id}}).then(res=>{
            let data=res.data;
            if(data.head.code=='10000'){
                this.goodsInfo=data.body;
                if(this.goodsInfo.sku[0]){
                    this.price_attr_second=this.goodsInfo.sku[0].attr_name; 
                }
                
                this.attrList=JSON.parse(JSON.stringify(this.goodsInfo)).attr;
                // 为表单绑定属性
                //     attr_input_type==0  input  
                //     attr_input_type==1  && attr_type==2 checkbox 
                //     attr_input_type==1  && attr_type==0 select
                //     attr_input_type==2  textarea    
                for(let i in this.attrList){
                    let attr_id=this.attrList[i].attr_id;
                    let attr_input_type=this.attrList[i].attr_input_type;
                    let attr_type=this.attrList[i].attr_type;
                    if(!this.addForm.attrs.hasOwnProperty(attr_id)){
                        if(attr_input_type==1&&attr_type==2){
                            this.addForm.attrs[attr_id]=[];
                        }else{
                            this.addForm.attrs[attr_id]='';
                        } 
                    }
                }
                this.addForm.attrs=JSON.parse(JSON.stringify(this.addForm)).attrs;
                if(this.actionType=='add'){

                }else if(this.actionType=='draft'){
                    this.getDraftDetail()
                }else if(this.actionType=='edit'||this.actionType=='copy'){
                    this.getGoodsDetail();
                }                
                
        
            }
        })
    },

    //价格属性
    atttr_price_unique(index,val){
        if(val.trim()==''){
            return false;
        }
        if(val.search(/[^a-zA-Z&\s]/)>-1){
            this.$message({type:'error',message:'价格属性只允许输入纯字母、&符号、空格'});
            this.addForm.color_images[index].color='';
            return false;
        }    
        //属性值重复
        for(let i in this.addForm.color_images){
            let it=this.addForm.color_images[i];          
            if((it.color.toLowerCase()==val.trim().toLowerCase())&&i!=index){
                this.$message({type:'error',message:'价格属性值不能重复'});                
                this.addForm.color_images[index].color='';
                break;
            }
        }
    },     
    querySearch(queryString, cb) {
        
        var colors = ColorList.color;
        var _color = queryString ? colors.filter(this.createFilter(queryString)) : colors;
        // 调用 callback 返回建议列表的数据
        cb(_color);
    },
    createFilter(queryString) {
        return (color) => {
          return (color.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
        };
    },
    handleAvatarSuccess(res,file,fileList) {
        let _fileList=fileList;
        let tmp=file;
        if(res.code !=200){
            this.$message.error(res.msg);
            _fileList.splice(0,1) ;                      
        }
       
    },
   handleAvatarRemove(file){

    },
    handleAvatarExceed(){
        this.$message.error('颜色属性图片只能上传一张，请删除后再上传');
    },    
    beforeAvatarUpload(file) {
       
        const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isJPG) {
            this.$message.error('上传图片请使用JPG格式！');
        }
        if (!isLt2M) {
            this.$message.error('上传头像图片大小不能超过 2MB!');
        }
        
        return isJPG && isLt2M;
        
    },
    //删除价格属性
    attr_price_delete(index,type){     
        this.$delete(this.addForm.color_images,index)
    },
    //增加颜色属性
    attr_price_add(type){
        this.addForm.color_images.push({
            gallery_id:'new_'+new Date().getTime(),
            color:'',
            img:'',
        
        });
      
    },
    
    //sku 操作
    actionSku(type,data){
        switch(type){
            case 'add'://增加sku
                this.addForm.skus.push({
                    "product_id": 'new_'+new Date().getTime(),
                    "product_title": "",
                    "product_price": '',
                    "product_number":'',
                    "color": "",
                    "size": ""
                })
            break;
            case 'delete':

                this.$delete(this.addForm.skus,data);
            break; 
        }
    },
    //批量修改sku
    skuBatchAction(type){
        if(this.skuBatch[type]==''){
            return false;
        }
        for(let i in this.addForm.skus){
            let it=this.addForm.skus[i];
            it[type]=this.skuBatch[type];
        }
        this.skuBatch[type]="";
    },
    handleView2 (name) {
        this.imgName = name;
        this.visible = true;
    },  
    handleImg (type,data) {
        const fileList = this.$refs.upload2.fileList;
        const index=fileList.indexOf(data);
        window.clearTimeout(this.setTimeoutId); 
       this.setTimeoutId=setTimeout(()=>{
            switch(type){
                case 'remove':              
                    fileList.splice(fileList.indexOf(data), 1);
                break;
                case 'left':
                    if(index==0) break;
                    let imageP=JSON.parse(JSON.stringify(fileList[index-1]));
                    fileList.splice(index-1,1);
                    fileList.splice(index,0,imageP);                  
                break;
                case 'right':
                    console.log(fileList.length-1,index)
                    if(index==(fileList.length-1)) break;
                    let imageN=JSON.parse(JSON.stringify(fileList[index+1]));
                    fileList.splice(index+1,1);
                    fileList.splice(index,0,imageN);                  
                break;
                case 'master':
                    if(index==0) break;
                    let image=JSON.parse(JSON.stringify(fileList[index]));
                    fileList.splice(index,1);
                    fileList.unshift(image);       
                break;                         
            }
       })

        
    },
    handleSuccess2 (res, file) {
        const fileList = this.$refs.upload2.fileList;
        if(res.code==200){
            file.url = this.goodsInfo.img_base_url+'/'+res.data;
            file.name = res.data;
        }else{
            this.$message.error(file.name+res.msg);
            this.$refs.upload2.fileList.splice(fileList.indexOf(file), 1);
        }
        
    },
    handleFormatError (file) {
        this.$message.error('上传图片请使用JPG格式！');
    },
    handleMaxSize (file) {
        this.$message.error('上传头像图片大小不能超过 2MB!');
    },
   
    //输入数字校验
    checkNumber(type,decimal){

        let tmp=type.split('.');
        switch(tmp.length){
            case 1:
            break;
            case 2:
              if(this[tmp[0]][tmp[1]]==''){
                  return false;
              }  
              let val= this[tmp[0]][tmp[1]]; 
              this[tmp[0]][tmp[1]]=isNaN(Number(val))?'':Number(val).toFixed(decimal); 
              if(type=='skuBatch.product_number'&&this[tmp[0]][tmp[1]]!=''){
                  this[tmp[0]][tmp[1]]=Math.abs(this[tmp[0]][tmp[1]]);
              } 
            break;
            case 3:
            break;
        }
        
    },
    checkSkuNumber(type,decimal,index){
        let val=this.addForm.skus[index][type];
        if(val==''){
            return false;
        }
        this.addForm.skus[index][type]=isNaN(Number(val))?'':Number(val).toFixed(decimal);
        if(type=='product_number'&&this.addForm.skus[index][type]!=''){
            this.addForm.skus[index][type]=Math.abs(this.addForm.skus[index][type]);
        }
    },
    //sku 第二属性输入限制
    checkSkuAttr(index){
        let val=this.addForm.skus[index].size;
        
        if(val.search(/[^a-zA-Z0-9-_#\(\)&\s]/)>-1){//# _  & ()-
            this.$message.error(this.price_attr_second+'属性值只允许输入字母数字# _  & ()-');
            this.addForm.skus[index].size='';
        }
    },
    //查询商品信息
    getGoodsDetail(){
        let params={goods_id:this.addForm.goods_id,to_copy:this.actionType=='copy'?1:0};
        this.axios.post('/proxy/fv/supplier/goods.php?act=getGoodsDetail',{body:params}).then(res=>{
            let data=res.data;
            if(data.head.code=='10000'){
                let detail=JSON.parse(JSON.stringify(data.body));
                this.addForm.common=data.body.common;
                this.addForm.attrs=Object.assign(this.addForm.attrs,detail.attrs);//渲染普通属性
                //sku的所有的颜色属性
                let color_attrs=[];
                for(let i in detail.skus){
                    let it=detail.skus[i];
                    if(!color_attrs.includes(it.color.toLowerCase())){
                        color_attrs.push(it.color.toLowerCase());
                    }
                }
                //颜色属性渲染                
                this.color_images_fileList=[];
                for(let i in detail.color_images){
                    let it=detail.color_images[i];
                   
                    this.color_images_fileList.push([{name:it.image,url:this.goodsInfo.img_base_url+'/'+it.image}]);//渲染颜色图片
                }
                this.addForm.color_images=detail.color_images;
                
                //渲染skus                
                this.addForm.skus=detail.skus;
                //渲染商品图片
                this.addForm.common_images=detail.common_images;
              
              
                for(let i in detail.common_images){
                    let it=detail.common_images[i];
                    
                    this.uploadList.push({
                        name:it,
                        url:this.goodsInfo.img_base_url+'/'+it,
                        status:"finished"
                    })
                };
                
               
               
                
                

               

            }
        })
    }, 
     //查询草稿详情
    getDraftDetail(){
        let url;
        let params;
        if(this.actionType=='draft'){
            url='getDraftDetail';
            params={draft_id:this.addForm.draft_id};
        }else if(this.actionType=='copy'){
            url='getDraftDetail';
            params={goods_id:this.addForm.goods_id};
        }
        this.axios.post('/proxy/fv/supplier/goods.php?act='+url,{body:params}).then(res=>{
            let data=res.data;
            if(data.head.code=='10000'){
                let detail=JSON.parse(JSON.stringify(data.body));
                this.addForm.common=data.body.common;
                this.addForm.attrs=Object.assign(this.addForm.attrs,detail.attrs);//渲染普通属性
                //sku的所有的颜色属性
                let color_attrs=[];
                for(let i in detail.skus){
                    let it=detail.skus[i];
                    if(!color_attrs.includes(it.color.toLowerCase())){
                        color_attrs.push(it.color.toLowerCase());
                    }
                }
                //颜色属性渲染                
                this.color_images_fileList=[];
                for(let i in detail.color_images){
                    let it=detail.color_images[i];
                    this.color_images_fileList.push([{name:it.image,url:this.goodsInfo.img_base_url+'/'+it.image}]);//渲染颜色图片
                }
                this.addForm.color_images=detail.color_images;
                
                //渲染skus                
                this.addForm.skus=detail.skus;
                //渲染商品图片
                this.addForm.common_images=detail.common_images;
                
                for(let i in detail.common_images){
                    let it=detail.common_images[i];
                    this.uploadList.push({
                        name:it,
                        url:this.goodsInfo.img_base_url+'/'+it,
                        status:"finished"
                    })
                };
            }
        })
    },    
    // 提交表单
    submit(type){
        
        let formData=JSON.parse(JSON.stringify(this.addForm));
        //url api 地址
        let actionType='';
        if(type=='add'||type=='audit'){
            if(this.actionType=='add'||this.actionType=='draft'||this.actionType=='copy'){
                actionType='insert';
            }else if(this.actionType=='edit'){
                actionType='update';
            }
            if(type=='audit'){
                formData.approval=true;
            }
        }else if(type=='draft'){
            actionType='saveDraft'
        }
        //提交商品校验，草稿不需要校验
        if(type!='draft'){
            this.$refs['form-add'].validate((valid,field)=>{
                if(!valid){
                    let fields=Object.keys(field);
                    document.getElementById('tab-'+fields[0]).focus();
                    return false;
                }
                if(formData.common.desc.replace(/<[^>]+>/g, '').replace(/&nbsp;/g,'').trim()==''){
                    this.$message.error('商品详细描述必须填写');
