    }
    .chunk-price-attr{
        .chunk-content{
            padding:10px 50px;
        }
        .item-title,.sku-title{
            color:#222;
            font-size:16px;
            padding:10px 0;
            padding-top:20px;
            position: relative;
            .item-button{
                position: absolute;
                top:35px;
                right: 10px;
            }
        }
        .el-table th>.cell{
            font-weight:normal;
            color:#222;
        }
        .avatar-uploader .el-upload {
            border:none;
            border-radius: 6px;
            cursor: pointer;
            position: relative;
            overflow: hidden;
            display: inline-block;
            vertical-align: middle;
            width:auto;
            height: auto;
            border:none;
            line-height:normal;
        }
        
        .avatar-uploader .el-upload:hover {
            border-color: #409EFF;
        }
        
        .el-upload-list--picture-card .el-upload-list__item {
            width: 100px;
            height: 100px;
            margin-bottom:-5px;
        }  
        .el-upload-list{
            display: inline-block;
            vertical-align: middle;
        } 
        .sku-batch{
            margin-top:5px;
            margin-bottom:15px;
            .el-input-number{
                width: 150px;
            }
            position: relative;
            .item-button{
                position: absolute;
                top: 0px;
                right: 10px;
            }
        }
          
    }
    .chunk-product-pic{
        .upload-box{
            padding:15px 50px;
        }

        .el-upload-list .el-upload-list__item{
            &:nth-of-type(1){
                .el-icon-zoom-in{
                    &::before{
                        content:'主图';
                    }
                }
            }
        }
        .el-upload-list--picture-card{
            display:inline-block;           
        }
        .el-upload-list__item-actions:hover span{
            display: block;
        }
        .el-upload-list__item-actions{
            &::after{
                display:none;
            }
        }
        .el-upload-list__item-preview{
            display:block;
            margin-top:40px;
        }
        .el-icon-zoom-in{
            font-size:16px;
            &::before{
                content:'设置为主图';
            }
        }
        .el-upload-list--picture-card .el-upload-list__item-actions span+span{
            margin-left:0;
        }        
    }
    .submit-box{
        padding-top:20px;
        padding-bottom:40px;
        .submit-button{
            text-align:center;
        }
    }
    .el-form-item__error{
        padding-left:0;
    }

    .demo-upload-list{
        display: inline-block;       
        text-align: center;    
        background: #fff;
        position: relative;
        box-shadow: 0 1px 1px rgba(0,0,0,.2);
        margin-right: 10px;
        margin-bottom:10px;
        padding:10px;
        width: 142px;
        height: 172px;
        overflow: hidden;

        img{
            width:120px;
            height: 120px;
            border: 1px solid transparent;
            border-radius: 8px;
            overflow: hidden;
            cursor: pointer;
        }
        .demo-upload-list-cover{
            margin-top:10px;
            i,span{
                color: #222;
                font-size: 18px;
                cursor: pointer;
                margin: 0 2px;
                display:inline-block;
                vertical-align:middle; 
                               
            }
            
            .el-icon-caret-left{
                margin-left:-10px;
            }
        }
        .master{
          position: absolute;
          right: -40px;
          top: -40px;
          background-color: #67C23A;
          color: #fff;
          text-align: center;
          -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
          width: 80px;
          height: 80px;
          display: inline-block;  
          padding-top: 1px;
          box-sizing: border-box;
          padding-top: 56px;
          letter-spacing: 2px;
          opacity: 0.9;
        }
    }
   
   
    .demo-upload-list-cover span{
        font-size:14px;
    }  
    .ivu-upload{
        vertical-align: top;
    }   
    .ivu-upload-drag{
    background: #fff;
    border: 1px dashed #dddee1;
    border-radius: 4px;
    text-align: center;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    transition: border-color .2s ease;
        .ivu-upload-input{
            display:none;
        }
    }
}
</style>



